.form {
  .offset(25px 0 30px 0; 0 20px);

  @media (min-width: @tablet-width) {
    margin: 0 30px 70px 70px;
    padding: 0;
  }

  @media (min-width: @desktop-width) {
    margin: 0 110px 80px 110px;
  }
}

.form__title {
  .offset(0; 0 0 37px 0);
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: @black;

  @media (min-width: @tablet-width) {
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 54px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 56px;
    padding-top: 12px;
  }
}

.form__description {
  .offset(0 54px 0 0; 0 0 39px 0);
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: @black;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    margin-bottom: 68px;
    padding-right: 344px;
    font-size: 20px;
    line-height: 30px;
  }

  @media (min-width: @desktop-width) {
    padding-right: 897px;
  }
}

.form__inner {
  .offset(0; 0 0 24px 0);

  @media (min-width: @tablet-width) {
    margin: 0;
    padding-right: 36px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 570px 570px;
    grid-gap: 80px;
    margin-bottom: 69px;
    padding-right: 0;
  }
}

@media (max-width: @mobile-width-only) {
  .form__border::before {
    content: "";
    position: absolute;
    left: -10%;
    width: 120%;
    height: 1px;
    background-color: @grey_light;
  }
}

.form__fieldset {
  .offset(0; 0);
  .border(0; 0; 0; 0);
}

.form__fieldset--data {
  position: relative;
  padding-bottom: 25px;

  &::before {
    bottom: 0;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 72px;
    padding-bottom: 0;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 0;
  }
}

.form__fieldset--programm {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  .offset(22px 0 19px 0; 0);

  &::before {
    bottom: 0;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 75px;
    padding: 25px 0 25px 28px;
    border: 1px solid @special_grey-6;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 0;
    padding-top: 22px;
    padding-left: 26px;
  }
}

.form__fieldset--owner {
  margin-bottom: 24px;

  @media (min-width: @tablet-width) {
    margin-bottom: 65px;
    padding-right: 34px;
  }

  @media (min-width: @desktop-width) {
    padding-right: 0;
  }
}

.form__fieldset-inner {
  .offset(0; 0);

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 570px 572px;
    grid-gap: 78px;
  }
}

.form__fieldset--comment {
  margin-bottom: 23px;

  @media (min-width: @tablet-width) {
    margin-bottom: 65px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 67px;
  }
}

.form__fieldset--optionally {
  .offset(0; 0 0 36px 0);

  @media (min-width: @tablet-width) {
    margin-bottom: 54px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 41px;
  }
}

.form__legend {
  position: relative;
  width: 100%;
  .offset(0; 0 0 36px 0);
  font-size: 24px;
  line-height: 26px;
  color: @black;

  @media (min-width: @tablet-width) {
    margin-bottom: 76px;
    font-size: 36px;
    line-height: 36px;
  }
}

.form__legend::before,
.form__legend::after {
  content: "";
  position: absolute;
}

.form__legend--line::before {
  width: 100%;
  height: 2px;
  right: -20px;
  background-color: @basic-green;

  @media (min-width: @tablet-width) {
    right: -30px;
  }

  @media (min-width: @desktop-width) {
    right: -110px;
  }
}

.form__legend--line::after {
  @media (min-width: @tablet-width) {
    top: 22px;
    left: -70px;
    width: 30px;
    height: 2px;
    background-color: @basic-green;
  }

  @media (min-width: @desktop-width) {
    top: 20px;
    left: -110px;
    width: 70px;
  }
}

.form__legend--line--owner {
  @media (max-width: @mobile-width-only) {
    padding-right: 79px;
  }
}

.form__legend--line--owner::before {
  top: 17px;

  @media (min-width: @tablet-width) {
    top: 22px;
    right: -64px;
  }

  @media (min-width: @desktop-width) {
    top: 20px;
    right: -110px;
  }
}

.form__legend--line-comment {
  @media (min-width: @tablet-width) {
    padding-right: 0;
  }
}

.form__legend--line-comment::before {
  top: 15px;

  @media (min-width: @tablet-width) {
    top: 22px;
  }
}

.form__legend--line-comment::after {
  @media (min-width: @desktop-width) {
    top: 22px;
  }
}

.form__legend--line-optionally {
  @media (min-width: @tablet-width) {
    margin-bottom: 57px;
  }
}

.form__legend--line-optionally::before {
  top: 17px;

  @media (min-width: @tablet-width) {
    top: 22px;
  }
}

.form__legend--line-optionally::after {
  @media (min-width: @desktop-width) {
    top: 22px;
  }
}

.form__legend--title {
  position: relative;
  display: block;
  padding-right: 15px;
  background-color: @white;

  @media (min-width: @tablet-width) {
    padding-right: 25px;
  }
}

.form__legend--owner {
  max-width: 360px;

  @media (min-width: @tablet-width) {
    max-width: 553px;
  }
}

.form__legend--comment {
  @media (max-width: @mobile-width-only) {
    max-width: 142px;
  }

  @media (min-width: @tablet-width) {
    max-width: 220px;
    padding-right: 25px;
  }
}

.form__legend--optionally {
  max-width: 164px;

  @media (min-width: @tablet-width) {
    max-width: 252px;
  }
}

.form__elements {
  display: flex;
  .offset(0; 0 0 14px 0);
}

.form__elements:last-of-type {
  margin-bottom: 0;
}

.form__elements--data {
  @media (min-width: @tablet-width) {
    margin-bottom: 20px;
  }
}

.form__elements--slim {
  margin-bottom: 8px;
  margin-right: 25px;

  @media (min-width: @tablet-width) {
    margin-right: 125px;
  }
}

.form__elements--weight-gain {
  margin-bottom: 11px;
  margin-right: 25px;

  @media (min-width: @tablet-width) {
    margin-right: 125px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 13px;
  }
}

.form__elements--owner:first-child {
  @media (min-width: @tablet-width) {
    margin-bottom: 20px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 0;
  }
}

.form__elements--textarea {
  @media (min-width: @tablet-width) {
    padding-right: 36px;
  }
}

.form__elements--optionally {
  .offset(0; 0 0 12px 0);

  &:last-child {
    .offset(0; 0);
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 32px;
    margin-right: 123px;

    &:nth-child(3) {
      margin-right: 218px;
      margin-bottom: 0;
    }
  }

  @media (min-width: @desktop-width) {
    min-width: 221px;
    margin: 0 104px 20px 0;

    &:nth-child(2n + 1) {
      margin-right: 104px;
    }
  }
}

.form__label {
  .offset(14px 0 0 0; 0 13px 0 0);
  line-height: 18px;
  text-transform: uppercase;
  color: @special_dark-grey;

  @media (min-width: @tablet-width) {
    margin-right: 16px;
    padding-top: 11px;
    font-size: 20px;
    line-height: 30px;
  }
}

.form__input:disabled + .form__label {
  opacity: 0.3;
}

.form__label--data {
  margin-right: 15px;

  @media (min-width: @tablet-width) {
    margin-right: 19px;
  }
}

.form__label--programm {
  padding-top: 7px;
  color: @special_dark-grey-2;

  @media (min-width: @desktop-width) {
    padding-top: 5px;
  }
}

.form__label--email {
  margin-right: 11px;
  padding-top: 10px;

  @media (min-width: @tablet-width) {
    margin-right: 20px;
  }
}

.form__label--phone {
  margin-right: 10px;
  padding-top: 11px;

  @media (min-width: @tablet-width) {
    margin-right: 15px;
  }
}

.form__label--optionally {
  padding-top: 5px;
  margin-right: 0;
  color: @black;
}

.form__input {
  position: relative;
  width: 147px;
  height: 45px;
  flex-grow: 2;
  .offset(16px 76px 15px 14px; 0);
  border: 1px solid @special_grey-6;
  font-family: "Oswald", "Arial", sans-serif;
  .typography(normal; 400; 16px; 18px);
  color: @special_dark-grey;

  @media (min-width: @tablet-width) {
    height: 52px;
    padding: 18px 38px 15px 18px;
    font-size: 20px;
    line-height: 30px;
  }

  @media (min-width: @desktop-width) {
    padding: 14px 76px 15px 20px;
  }

  &::placeholder {
    opacity: 1;
    color: @special_dark-grey;
    text-transform: uppercase;
  }
}

.form__input,
.form__textarea {
  cursor: pointer;

  &:hover {
    border-color: @special_grey;
  }

  &:focus {
    border: 2px solid @special_dark-grey;
    outline: none;
  }

  &:invalid {
    border: 2px solid @status_error;
  }
}

.form__input--number {
  padding-left: 16px;
  padding-right: 50px;
  appearance: textfield;

  @media (min-width: @tablet-width) {
    padding: 14px 38px 15px 19px;
  }
}

.form__input--age {
  padding-right: 18px;

  @media (min-width: @desktop-width) {
    padding-left: 16px;
  }
}

.form__input--radio {
  flex-grow: 0;
