  background-image: url("data:image/svg+xml,%3Csvg width='30' height='31' viewBox='0 0 30 31' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M29.5 15.2744C29.5 23.3126 23.0065 29.826 15 29.826C6.99349 29.826 0.5 23.3126 0.5 15.2744C0.5 7.23613 6.99349 0.722778 15 0.722778C23.0065 0.722778 29.5 7.23613 29.5 15.2744Z' stroke='%23EAEAEA'/%3E%3C/svg%3E");

  @media (min-width: @tablet-width) {
    background-image: url("data:image/svg+xml,%3Csvg width='41' height='40' viewBox='0 0 41 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M40.4175 20C40.4175 30.7701 31.6977 39.5 20.9425 39.5C10.1874 39.5 1.46753 30.7701 1.46753 20C1.46753 9.22986 10.1874 0.5 20.9425 0.5C31.6977 0.5 40.4175 9.22986 40.4175 20Z' stroke='%23EAEAEA'/%3E%3C/svg%3E");
  }
}

.form__input--icon::before {
  content: "";
  position: absolute;
  right: 0;
  background-repeat: no-repeat;
}

.form__input--email {
  flex-grow: 2;
  padding-right: 35px;
  background-image: url("data:image/svg+xml,%3Csvg width='20' height='15' viewBox='0 0 20 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline x1='1.65' y1='5.84766' x2='1.65' y2='13.8477' stroke='%23222222' stroke-width='1.3'/%3E%3Cline x1='18.65' y1='5.84766' x2='18.65' y2='13.8477' stroke='%23222222' stroke-width='1.3'/%3E%3Cline x1='1' y1='13.1977' x2='19' y2='13.1977' stroke='%23222222' stroke-width='1.3'/%3E%3Cline x1='1' y1='1.19766' x2='19' y2='1.19766' stroke='%23222222' stroke-width='1.3'/%3E%3Cpath d='M18.35 3.02975L10 9.5242L1.65 3.02975V1.49766H4.5L10 1.49766H15H18.35V3.02975Z' stroke='%23222222' stroke-width='1.3'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: 93% 50%;

  @media (min-width: @tablet-width) {
    padding: 15px 0 15px 20px;
    background-position: 96.5% 49%;
  }
}

