  background-image: url("data:image/svg+xml,%3Csvg width='20' height='15' viewBox='0 0 20 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline x1='1.65' y1='5.84766' x2='1.65' y2='13.8477' stroke='%2368B738' stroke-width='1.3'/%3E%3Cline x1='18.65' y1='5.84766' x2='18.65' y2='13.8477' stroke='%2368B738' stroke-width='1.3'/%3E%3Cline x1='1' y1='13.1977' x2='19' y2='13.1977' stroke='%2368B738' stroke-width='1.3'/%3E%3Cline x1='1' y1='1.19766' x2='19' y2='1.19766' stroke='%2368B738' stroke-width='1.3'/%3E%3Cpath d='M18.35 3.02975L10 9.5242L1.65 3.02975V1.49766H4.5L10 1.49766H15H18.35V3.02975Z' stroke='%2368B738' stroke-width='1.3'/%3E%3C/svg%3E");
}

.form__input--email:invalid {
  background-image: url("data:image/svg+xml,%3Csvg width='20' height='15' viewBox='0 0 20 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline x1='1.65' y1='5.84766' x2='1.65' y2='13.8477' stroke='%23FF8282' stroke-width='1.3'/%3E%3Cline x1='18.65' y1='5.84766' x2='18.65' y2='13.8477' stroke='%23FF8282' stroke-width='1.3'/%3E%3Cline x1='1' y1='13.1977' x2='19' y2='13.1977' stroke='%23FF8282' stroke-width='1.3'/%3E%3Cline x1='1' y1='1.19766' x2='19' y2='1.19766' stroke='%23FF8282' stroke-width='1.3'/%3E%3Cpath d='M18.35 3.02975L10 9.5242L1.65 3.02975V1.49766H4.5L10 1.49766H15H18.35V3.02975Z' stroke='%23FF8282' stroke-width='1.3'/%3E%3C/svg%3E%0A");
}

.form__input--phone {
