.form__textarea {
  width: 100%;
  min-height: 150px;
  .offset(9px 12px 0 15px; 0);
  font-family: "Oswald", "Arial", sans-serif;
  .typography(normal; 400; 16px; 18px);
  border: 1px solid @special_grey-6;
  resize: none;
  color: @special_dark-grey;
  text-transform: uppercase;

  &::placeholder {
    color: @special_dark-grey;
    opacity: 1;
  }

  @media (min-width: @tablet-width) {
    padding: 10px 0 102px 18px;
    font-size: 20px;
    line-height: 30px;
  }
}

.form__optionally-inner {
  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
  }
}

.form__submit {
  @media (min-width: @tablet-width) {
    width: 565px;
    display: flex;
    flex-wrap: wrap;
    .offset(0; 0);
  }

  @media (min-width: @desktop-width) {
    width: 829px;
  }
}

.form__note {
  .offset(0; 0);
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  color: @black;
  text-align: center;

  @media (min-width: @tablet-width) {
    padding-top: 16px;
    font-size: 16px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 19px;
  }
}

.form__button-send {
  width: 100%;
  .offset(10px 0 10px; 0 0 21px 0);
  text-align: center;
  border: none;
  font-family: "Oswald", "Arial", sans-serif;
  .typography(normal; 400; 16px; 20px);

  @media (min-width: @tablet-width) {
    width: 282px;
    .offset(16px 0; 0 61px 0 0);
    font-size: 20px;
    line-height: 20px;
  }

  @media (min-width: @desktop-width) {
    width: 568px;
    margin-right: 81px;
  }
}
