@font-face {
  font-family: "Oswald";
  font-display: swap;
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff2") format("woff2");
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  font-size: 0;
}
.wrapper {
  padding: 0;
  margin: 0 20px;
}
@media (min-width: 768px) {
  .wrapper {
    padding: 0;
    margin: 0 30px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    padding: 0;
    margin: 0 110px;
  }
}
.button {
  display: block;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 18px;
  background-color: #68B738;
  color: #ffffff;
  text-transform: uppercase;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  background-color: #5eaa2f;
  color: #8ec36d;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.page {
  max-width: 1440px;
  height: 100%;
  padding: 0;
  margin: 0 auto;
  overflow-x: hidden;
}
.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  background-color: #ffffff;
  color: #444444;
}
.page-header {
  padding: 12px 0 0 0;
  margin: 0;
}
@media (min-width: 768px) {
  .page-header {
    margin: 0 auto;
    padding-top: 25px;
  }
}
@media (min-width: 1440px) {
  .page-header {
    padding-top: 54px;
  }
}
@media (min-width: 1440px) {
  .page-header__background {
    height: 694px;
    background-image: url("../img/desktop-images/background-header.jpg");
    background-repeat: no-repeat;
    background-size: 720px 694px;
    background-position: right, bottom;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .page-header__background {
    background-image: url("../img/desktop-images/background-header@2x.jpg");
  }
}
.page-header__container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}
@media (min-width: 768px) {
  .page-header__container {
    display: grid;
    grid-template-columns: 173px 1fr;
    column-gap: 95px;
    margin-bottom: 50px;
  }
}
@media (min-width: 1440px) {
  .page-header__container {
    grid-template-columns: 202px 479px;
    column-gap: 540px;
  }
}
.page-header__logo {
  position: relative;
  display: block;
  width: 190px;
}
@media (max-width: 320px) {
  .page-header__logo::after {
    content: "";
    position: absolute;
    top: 53px;
    left: -20px;
    width: 320px;
    height: 1px;
    background-color: #e6e6e6;
  }
}
@media (min-width: 768px) {
  .page-header__logo {
    width: 173px;
    height: 52px;
    padding: 0;
    margin-left: 0;
  }
}
@media (min-width: 1440px) {
  .page-header__logo {
    width: 202px;
    height: 59px;
  }
}
.page-header__logo-img {
  padding: 0;
  margin: 0 0 10px 0;
  width: 190px;
  height: 38px;
}
@media (min-width: 768px) {
  .page-header__logo-img {
    width: 173px;
    height: 51px;
  }
}
@media (min-width: 1440px) {
  .page-header__logo-img {
    width: 202px;
    height: 60px;
  }
}
.page-header__logo-img:hover {
  opacity: 0.8;
}
.page-header__logo-img:active {
  opacity: 0.6;
}
.page-header__promo {
  position: relative;
  padding: 0;
  margin: 0 0 22px;
}
@media (min-width: 768px) {
  .page-header__promo {
    margin: 0;
  }
}
.page-header__promo-inner {
  height: 302px;
  padding: 0 20px;
  margin: 0 0 138px;
  background-color: #63a73b;
  background-image: url("../img/mobile-images/promo__illustration.jpg");
  background-repeat: no-repeat;
  background-size: 320px 302px;
}
@media (min-resolution: 2dppx) {
  .page-header__promo-inner {
    background-image: url("../img/mobile-images/promo__illustration@2x.jpg");
  }
}
@media (min-width: 321px) {
  .page-header__promo-inner {
    display: flex;
    flex-direction: column;
    background-position: 50% 50%;
    background-size: cover;
  }
}
@media (min-width: 768px) {
  .page-header__promo-inner {
    min-height: 810px;
    margin-bottom: 0;
    background-color: transparent;
    background-image: none;
  }
}
@media (min-width: 1440px) {
  .page-header__promo-inner {
    min-height: 499px;
    padding: 50px 93px 0;
  }
}
.page-header__promo-title {
  display: inline-block;
  vertical-align: middle;
  padding: 26px 0 0 0;
  margin: 0 0 25px 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #ffffff;
  text-align: center;
}
@media (min-width: 321px) {
  .page-header__promo-title {
    width: 250px;
    margin: 0 auto 25px auto;
  }
}
@media (min-width: 768px) {
  .page-header__promo-title {
    width: 531px;
    padding: 0 0 0 97px;
    margin: 0 0 43px 0;
    font-size: 60px;
    line-height: 60px;
    color: #000000;
    text-align: left;
  }
}
@media (min-width: 1440px) {
  .page-header__promo-title {
    margin-bottom: 40px;
  }
}
.page-header__subtitle {
  padding: 0 0 0 40px;
  margin: 0 0 29px;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: #ffffff;
  text-transform: uppercase;
}
@media (min-width: 321px) {
  .page-header__subtitle {
    width: 198px;
    padding: 0;
    margin: 0 auto 20px auto;
  }
}
@media (min-width: 768px) {
  .page-header__subtitle {
    width: 380px;
    padding-left: 97px;
    margin: 0 0 20px 0;
    font-size: 20px;
    line-height: 20px;
    color: #000000;
  }
}
@media (min-width: 1440px) {
  .page-header__subtitle {
    margin-bottom: 95px;
  }
}
.page-header__promo-image {
  width: 280px;
  height: 270px;
  display: inline-block;
  vertical-align: middle;
  padding: 0;
  margin: 0;
}
@media (min-width: 321px) {
  .page-header__promo-image {
    display: block;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .page-header__promo-image {
    width: 709px;
    height: 609px;
    margin-left: 10px;
  }
}
@media (min-width: 1440px) {
  .page-header__promo-image {
    width: 552px;
    height: 499px;
    margin-top: -357px;
    margin-left: 466px;
  }
}
.page-header__promo-programm {
  width: 280px;
  padding: 10px 0 10px 73px;
  margin: 0 20px;
  font-size: 14px;
  line-height: 18px;
}
@media (min-width: 321px) {
  .page-header__promo-programm {
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .page-header__promo-programm {
    position: absolute;
    top: 231px;
    left: 112px;
    width: 244px;
    padding: 18px 26px 16px 26px;
    margin: 0;
    font-size: 20px;
    line-height: 20px;
  }
}
@media (min-width: 1440px) {
  .page-header__promo-programm {
    top: 281px;
    left: 190px;
  }
}
.page-main {
  display: grid;
  grid-template-rows: min-content;
  align-content: start;
  padding: 0;
  margin: 0;
}
@media (min-width: 321px) {
  .main-nav {
    padding: 0;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .main-nav {
    padding: 16px 0 0 0;
    margin: 0 auto 24px 0;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    margin-bottom: 19px;
  }
}
.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  padding: 0;
  margin: 0;
  list-style-type: none;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
  }
}
.main-nav__button-toggle {
  position: absolute;
  top: 22px;
  right: 23px;
  display: block;
  width: 18px;
  height: 19px;
  cursor: pointer;
  border-top: 0;
  border-right: 0;
  border-bottom: 0;
  border-left: 0;
  background-color: transparent;
  z-index: 2;
}
@media (min-width: 768px) {
  .main-nav__button-toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__button-toggle::before {
  content: "";
  position: absolute;
  width: 18px;
  height: 2px;
  top: 9px;
  right: 0;
  box-shadow: 0 -6px #000000, 0 6px #000000;
  background-color: #000000;
  z-index: 10;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
.main-nav--opened .main-nav__button-toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 20px;
  background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline y1='-0.75' x2='24.7577' y2='-0.75' transform='matrix(0.686589 -0.727046 0.686589 0.727046 2.00171 20)' stroke='black' stroke-width='1.5'/%3E%3Cline y1='-0.75' x2='24.7577' y2='-0.75' transform='matrix(0.686589 0.727046 -0.686589 0.727046 1 2)' stroke='black' stroke-width='1.5'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  z-index: 10;
}
@media (max-width: 767px) {
  .main-nav--no-js .main-nav__button-toggle {
    display: none;
  }
  .main-nav--no-js .main-nav__list {
    display: block;
  }
}
.main-nav__item {
  position: relative;
  padding: 22px 50px 24px 49px;
  margin: 0;
}
@media (min-width: 768px) {
  .main-nav__item {
    padding: 0;
    margin-right: 35px;
  }
  .main-nav__item:nth-child(2) {
    margin-right: 37px;
  }
  .main-nav__item:nth-child(3) {
    margin-right: 0;
  }
  .main-nav__item:last-child {
    margin-right: 0;
  }
}
@media (min-width: 1440px) {
  .main-nav__item {
    padding: 0;
    margin: 0 35px 0 0;
  }
  .main-nav__item:nth-child(2) {
    margin-right: 32px;
  }
  .main-nav__item:last-child {
    margin-right: 0;
  }
}
.main-nav__item:first-child {
  padding: 24px 102px;
}
@media (min-width: 768px) {
  .main-nav__item:first-child {
    padding: 0;
    margin-right: 34px;
  }
}
@media (max-width: 767px) {
  .main-nav__item::after {
    content: "";
    position: absolute;
    top: 66px;
    left: -20px;
    width: 320px;
    height: 1px;
    background-color: #e6e6e6;
  }
  .main-nav__item:nth-child(2)::after {
    top: 64px;
  }
}
.main-nav__link {
  display: block;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
}
@media (min-width: 768px) {
  .main-nav__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .main-nav__link {
    padding-bottom: 5px;
    font-size: 20px;
    line-height: 30px;
  }
}
.main-nav__link:hover {
  opacity: 0.8;
}
.main-nav__link:focus {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 1;
  box-shadow: 0 2px 0 0 #68B738;
}
@media (min-width: 1440px) {
  .main-nav__link--color {
    color: #ffffff;
  }
}
.main-nav__link--color:active {
  box-shadow: 0 2px 0 0 #ffffff;
}
.programm {
  padding: 0;
  margin: 0 0 30px 0;
}
@media (min-width: 768px) {
  .programm {
    /*margin-top: 82px;*/
    margin-bottom: 47px;
  }
}
@media (min-width: 1440px) {
  .programm {
    margin-top: 82px;
    margin-bottom: 73px;
  }
}
@media (min-width: 640px) and (max-width: 767px) {
  .programm__wrapper {
    display: flex;
    flex-wrap: wrap;
  }
}
@media (min-width: 1440px) {
  .programm__wrapper {
    display: flex;
    flex-wrap: wrap;
  }
}
.programm__section {
  padding: 30px 33px 15px 20px;
  margin: 0 0 20px 0;
  background-color: #f2f2f2;
}
@media (min-width: 640px) and (max-width: 767px) {
  .programm__section {
    width: 290px;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .programm__section {
    padding: 77px 61px 53px 52px;
    margin: 0 0 30px 0;
  }
}
@media (min-width: 1440px) {
  .programm__section {
    width: 570px;
    padding: 77px 51px 55px 52px;
    margin: 0 80px 0 0;
  }
}
@media (min-width: 640px) and (max-width: 767px) {
  .programm__section:first-of-type {
    margin-right: 20px;
  }
}
.programm__section:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .programm__section:last-of-type {
    padding: 79px 28px 55px 52px;
    margin: 0;
  }
}
@media (min-width: 1440px) {
