.social {
  .offset(20px 0 15px 0; 0 auto);

  @media (max-width: @mobile-width-only) {
    display: flex;
    border-top: 1px solid @grey_light;
    border-bottom: 1px solid @grey_light;
  }

  @media (min-width: @tablet-width) {
    .offset(0; 0 131px 15px 0);
  }

  @media (min-width: 780px) and (max-width: 1439px) {
    margin: 0 auto 15px auto;
  }

  @media (min-width: @desktop-width) {
    margin: 0 387px 15px 0;
  }
}

.social__list {
  display: flex;
  flex-wrap: wrap;
  .offset(0; 0 auto);
  list-style-type: none;
}

.social__item {
  margin-right: 33px;

  &:first-child {
    margin-right: 36px;
  }

  &:last-child {
    margin-right: 0;
  }

  @media (min-width: @tablet-width) {
    margin-right: 21px;

    &:first-child {
      margin-right: 27px;
    }
  }
}

.social__link {
  display: block;
}

.social__logo {
  background-repeat: no-repeat;
}

